import React, { useState, useContext, useEffect } from "react";
import {
  Box,
  Breadcrumbs,
  Divider,
  Link,
  Stack,
  Typography,
} from "@mui/material";
import { NavLink, Outlet } from "react-router-dom";
import Content from "../components/Content";
import { CustomContext } from "../components/context/provider/CustomProvider";
import { useQuery } from "react-query";
import { getAllAssignedCoursesToLecturerByID } from "../components/api/assignedCourseApi";

function Tutor() {
  const { customState, customDispatch } = useContext(CustomContext);

  const [professionalID, setProfessionalID] = useState("");

  useEffect(() => {
    const proID = localStorage.getItem("professionalID");
    if (proID !== null) {
      setProfessionalID(proID);
    }
  }, [customState?.currentLecturerProfessionalID]);

  useQuery(
    ["assigned-course", professionalID],
    () => getAllAssignedCoursesToLecturerByID(professionalID),
    {
      enabled: !!professionalID,

      onSuccess: async (courses) => {
        const modifiedCourses = courses.map((course) => {
          return {
            id: course._id,
            programme: course.programme,
            code: course.course.id,
            course: course.course.label,
          };
        });

        await customDispatch({
          type: "assignedCourses",
          payload: modifiedCourses,
        });
      },
    }
  );

  return (
    <div className="tutor-container">
      <Content>
        <section>
          <Typography variant="h4">Tutor</Typography>
          <Box
            sx={{
              display: "flex",
              justifyContent: "flex-end",
            }}
          >
            <Breadcrumbs>
              <Link>Home</Link>
              <Typography>Tutor</Typography>
            </Breadcrumbs>
          </Box>
          <Divider />
          <Stack direction="row" spacing={3} paddingY={2}>
            <NavLink className="nav-link" to="/info/tutor">
              Course Registration
            </NavLink>
            {/* <NavLink className="nav-link" to="class">
              Class
            </NavLink>
            <NavLink className="nav-link" to="students">
              Students
            </NavLink> */}
          </Stack>
          <section>
            <Outlet />
          </section>
        </section>
      </Content>
    </div>
  );
}

export default Tutor;
